.knowledges{
    .knowledgesContent{
        width: 97%;
        min-height: $minHeight;
        position: relative;
        overflow: hidden;
        display: grid;
        grid-template-columns: 38% 1fr 1fr;
        grid-template-rows: 70% 30%;
        grid-template-areas: 
            "L E E"
            "S S H"
        ;

        .langagesFramework, .formation, .savoirEtre, .hobbies{
            background: $color-2;
            border-radius: $borderRadius;
            padding: .8rem;
        }

        h3{
            font-size: 1.4rem;
            margin-bottom: 1rem;
            font-family: $font-2;
            color: $color-5;
            text-transform: uppercase;
        }

        .langagesFramework{
            grid-area: L;
            margin-bottom: .5rem;
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr;
            grid-template-areas: 
            "L"
            ;

            .langagesDisplay{
                grid-area: L;
            }

            h3{
                margin-bottom: .3rem;
            }

            .years{
                font-size: .7rem;
                display: flex;
                justify-content: space-around;
                color: $white;
            

                span{
                    width: 36%;

                    &:nth-child(2){
                        text-align: center;
                        width: 20%;
                    }

                    &:nth-child(3){
                        text-align: right;
                        width: 44%;
                    }

                    &:nth-child(2):before,
                    &:nth-child(3):before{ 
                        content: '';
                        background: $white;
                        position: absolute;
                        height: 325px;
                        width: 1px;
                        transform: translate(9px ,19px);                    
                    }
                }
            }

            .langagesList{
                padding-top: .5rem;

                li{
                    color: $color-1;
                }

                .progressBar{
                    height: .4rem;
                    background: transparent;
                    border-radius: 50px;
                    margin-top: .2rem;
                    position: relative;

                    &:after{
                        content: '';
                        position: absolute;
                        background: $color-3;
                        border-radius: 50px;
                        height: .4rem;
                        width: 0;
                        animation: line 2s ease-out forwards;

                        @keyframes line {
                            to{
                                width:100%
                            }
                        }
                    }
                }
            }
        }

        .formation {
            grid-area: E;
            margin: 0 0 .5rem .5rem;
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 10% 1fr 1fr;
            grid-template-areas: 
              "1"
              "2"
              "3";
            
            h3 {
              grid-area: 1;
            }
            h4 {
              font-size: 1.1rem;
              color: $color-5;
            }
            h5 {
              color: $black;
              padding: 0.5rem 0 0.5rem 1rem;
            }
            p {
              text-align: justify;
              padding: 0 1rem;
            }
            .form-1 {
              grid-area: 2;
            }
            .form-2 {
              grid-area: 3;
            }
            .form-3{
              grid-area: 4;
            }
        }

        .savoirEtre {
            grid-area: S;
            margin-right: 0.5rem;

            h3{
                margin-bottom: 0 ;
            }
      
            .softList {
                display: flex;
                width: 80%;
                height: 100%;
                margin: 0 auto;
                justify-content: space-between;
      
                ul {
                    height: 80%;
                    display: grid;
        
                    i {
                        color: $color-5;
                        padding: 10px;
                    }
                }
            }
        }
          
        .hobbies {
            grid-area: H;
            padding-left: 1.5rem;

            h3{
                margin-bottom: .2rem ;
            }
            
            ul {
                height: 80%;
                display: grid;
      
                li {
                    i {
                    position: absolute;
                    color: $color-5;
                    }
                    span {
                    position: absolute;
                    transform: translateX(2.2rem);
                    }
                }
            }  
        }
      
        @media screen and (max-width: 950px){
            .knowledgesContent {
                h3 {
                    font-size: 1.2rem !important;
                }
                .languagesList {
                    padding-top: .2rem !important;
                }
                p {
                    font-size: .8rem;
                }
                .savoirEtre, .hobbies {
                    font-size: .8rem;
                }
            }
        }

        @media screen and (max-width: 850px){
            .knowledgesContent {
                h4 {
                    font-size: .9rem !important;
                }
                h5 {
                    font-size: .6rem !important;
                    padding: .2rem 1rem !important;
                }
            }
        }

        @media screen and (max-width: 500px){
            .knowledgesContent {
                display: grid !important;
                grid-template-columns: 1fr !important;
                grid-template-rows: 1fr 1fr 180px 180px !important;
                
                .languages {
                    grid-area: 1 !important;
                }
                .formation {
                    grid-area: 2 !important;
                    margin-left: 0 !important;
                }
                .savoirEtre {
                    grid-area: 3 !important;
                    margin-right: 0 !important;
                }
                .hobbies {
                    grid-area: 4 !important;
                    margin-top: .5rem !important;
                }
            }    
        }  
    }
}