.home{

  .homeContent{
        background: linear-gradient(115deg, $color-1, 70%, transparent 75%);
        width: 100%;
        min-height: 500px;
        border-radius: $borderRadius;
        position: relative;
        overflow: hidden;

        &:after{
            content: '';
            position: absolute;
            height: 100%;
            width: inherit;
            background: url(../../media/font.jpeg) no-repeat center/cover;
            z-index: -1;
            filter: brightness(75%);
        }

        .content{
            position: absolute;
            top: 20%;
            left: 13%;
            font-family: $font-1;

            h1{
                font-family: $font-3;
                font-size: 3rem;
                color: $color-5;
                text-transform: uppercase;
                text-shadow: 3px 6px 2px $textShawdow;
            }

            h2{
                font-family: $font-3;
                font-size: 2.5rem;
                color: $color-5;
                padding: 1.5rem 0 4rem;
                text-shadow: 1px 4px 1px $textShawdow;
            }

            .pdf {
              -webkit-animation: breathing .1s ease-in-out infinite;
              animation: breathing 4s ease-in-out infinite;
              -webkit-font-smoothing: antialiased;

              a{
                font-size: 1.5rem;
                background: $color-3;
                padding: .65rem 2rem;
                border-radius: $borderRadius;
                box-shadow: 0 5px 1px $textShawdow;  
              }
                
              @-webkit-keyframes breathing {
                0% {
                  -webkit-transform: scale(1);
                  transform: scale(1);
                }
                  
                25% {
                  -webkit-transform: scale(1);
                  -ms-transform: scale(1);
                  transform: scale(1);
                }

                60% {
                  -webkit-transform: scale(0.9);
                  -ms-transform: scale(0.9);
                  transform: scale(0.9);
                }
                  
                100% {
                  -webkit-transform: scale(1);
                  transform: scale(1);
                }
              }
                  
              @keyframes breathing {
                0% {
                  -webkit-transform: scale(1);
                  -ms-transform: scale(1);
                  transform: scale(1);
                }
                
                25% {
                  -webkit-transform: scale(1);
                  -ms-transform: scale(1);
                  transform: scale(1);
                }
                  
                60% {
                  -webkit-transform: scale(0.9);
                  -ms-transform: scale(0.9);
                  transform: scale(0.9);
                }
                  
                100% {
                  -webkit-transform: scale(1);
                  -ms-transform: scale(1);
                  transform: scale(1);
                }
              }
            }    
        }
    }
}
@media screen and (max-width: 850px){
  .homeContent {
    margin: 0 !important;
  }
}
@media screen and (max-width:580px){
  .homeContent {
    &:after {
    background: url('../../media/font.jpeg') no-repeat 60% 50%/cover !important;
    }
    h1 {
      font-size: 3rem !important;
    }
    h2 {
      font-size: 1.8rem !important;
    }
  }
}

