.sidebar{
    background: $color-1;
    font-family: $font-1;
    min-height: 50px;
    width: 264px;
    border-radius: $borderRadius;
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 250px 1fr 108px;
    grid-template-areas: 
        "1"
        "2"
        "3"
    ;

    .id{
        grid-area: 1;
        text-align: center;
        user-select: none;
        position: relative;

        .idContent{
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;

            img{
                border-radius: 50%;
                box-shadow: 0 0 0 4px $color-3, 0 0 0 8px $color-4, 0 0 0 12px $color-2;
                width: 150px;
                height: 150px;
            }

            h3{
                font-size: 1.3rem;
                text-transform: uppercase;
                letter-spacing: .2rem;
                margin-top: 1rem;
                color: $color-5;
            }
        }
    }

    .navigation{
        grid-area: 2;
        padding: 2rem 1rem;
        ul{
            height: 100%;
            display: grid;
        }
        li{
            padding: 0rem 0 1rem .4rem;
            font-size: 1.3rem;
            position: relative;
            &:hover i{
                transform: translate(3px, 3px);
            }
            a{
                color: $color-5;
            }
            .navActive{
                color: $color-6;
            }
            
            i{
                position: absolute;
                font-size: 0.95rem;
                transform: translateY(3px);
                transition: .2s ease;
            }

            span{
                margin-left: 2.7rem;
            }
        }
    }
}

.socialNetwork{
    grid-area: 3;

    ul{
        display: flex;
        width: 264px;
        padding: 0 14px;
        justify-content: space-around;
        
        li{
            position: center;
            font-size: 1.5rem;
            background: $color-5;
            color: $color-1;
            border-radius: 50%;
            padding: .5rem .8rem;
            transition: 0.3s;
            cursor: pointer;
            transform: translateX(3px);
            transition: .2s ease;

            a:visited{
                color: $color-1;
            }

            &:hover{
                transform: translate(3px, 3px);
                background: $color-4;
            }

            &:nth-child(1){
                padding: .5rem .85rem;
            }

            &:nth-child(3){
                padding: .5rem .75rem;
            }
        }    
    }
}

.signature{
    margin-top: 2rem;
    text-align: center;
    letter-spacing: .1rem;
    font-size: .7rem;

    p{
        color: $color-5;

        &:hover{
            color: $color-4;
            cursor: default;
        }
    }

     
}

@media screen and(max-width: 1920px) {
    .sidebar{
        height: 900px;
    }
}

@media screen and(max-width: 1600px) {
    .sidebar{
        height: 595px;
    }
}



@media screen and (max-width: 850px) {
    .sidebar{
        display: flex;
        height: 100%;
        margin-bottom: .2rem;
        width: 100%;

        .id, .socialNetwork{
            display: none;
        }

        .navigation{
            width: 100%;
            padding: .1rem 1rem;
            
            ul{
                display: flex;
                justify-content: space-around;

                span{
                    margin-left: 1.5rem !important;
                }

            }
        }
    }  
}

@media screen and (max-width: 850px) {
    .sidebar{
        ul{ 
            li{
                padding: 1rem 0 !important;
                
                &:hover i{
                    transform: translate(.25px, .25px) !important;
                }
            }

            i{
                font-size: 1.4rem !important;
                transform: translateY(-3px) !important;
            }
            span{
                display: none;
            }
        }
    }
}


