@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@300&family=Josefin+Sans:wght@100&family=Nova+Round&display=swap');

$font-1: 'Nova Round', cursive;
$font-2: 'Dosis', sans-serif;
$font-3: 'Josefin Sans', serif;

$color-1: #2a1d52;
$color-2: #6C77F4;
$color-3: rgb(99, 164, 233);
$color-4: #4FEDD2;
$color-5: #36D0EA;
$color-6:rgb(174, 243, 255);
$white: #ddd;
$black: #333;
$textShawdow: rgb(15, 15, 15);

$borderRadius: 25px;
$shadow: 0px 1px 3px #333;
$minHeight: 9vh;

@mixin verticalCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    margin: .5rem;
    font-family: $font-1;
    color: $black;
    height: 100%;
}

button{
    cursor: pointer;
}

li{
    list-style-type: none;
}

a{
    text-decoration: none;
    color: $black;
    cursor: pointer;
}

a:visited{
    color: $black;
}

.home, .knowledges, .portfolio, .contact{
    display: flex;

    @media screen and (max-width: 850px){
        display: block !important; 
    }
}

.homeContent, .knowledgesContent, .portfolioContent, .contactContent{
    margin-left: .4rem;

    @media screen and (max-width: 850px){
        margin-left: 10px;
    }
}